<template>
  <div id="box2">
    <div class="top">
      <div class="f20" v-if="$route.query.type==1">今日订单</div>
      <div class="f20" v-if="$route.query.type==2">今日收益</div>
      <div class="f20" v-if="$route.query.type==3">昨日收益</div>
      <div class="f20" v-if="$route.query.type==4">近七天收益</div>
      <div class="f20" v-if="$route.query.type==5">近一月收益</div>
      <div class="f20" v-if="$route.query.type==6">今年收益</div>
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad">
      <div class="list">
        <div v-for="(item,i) in list" :key="i" class="bbg">
          <div class="item">
            <span>酒店名称：{{item.hotel_name}}</span>
            <span>收益：{{item.allf_money}}</span>
          </div>
          <div class="item">
            <span>时间：{{item.create_time}}</span>
            <span>设备号：{{item.number}}</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getorder_detail_list } from '@/axios/getData'
import { List } from 'vant';
export default {
  data(){
    return{
      page:1,
      list:[],
      finished:false,
      loading:false,
      count:'',   // 总数
    }
  },
  components: {
    vanList: List
  },
  created() {
    // this.getdata()
  },
  methods:{
    async onLoad(){
      let arrs = JSON.parse(localStorage.getItem('shebei_arr'));
      if(this.loading){
        var res = await getorder_detail_list({arras:arrs.join(','), page:this.page, type:this.$route.query.type});
        console.log(res.data);
        this.count = res.data.count
        if(this.page ==1){
          this.list = res.data.list
        } else {
          this.list = this.list.concat(res.data.list)
        }
        this.loading = false;
        this.page++
        if(this.list.length>=this.count){
          this.finished = true
        }
      }
    }
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
#box2{
  height:100vh;
  background: #fff;
}
.top{
  width:100%;
  height:200px;
  color:#fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(45deg,#2FE0EF,#15BDDB);
  .info{
    display: flex;
    align-items: center;
    .span{
      display: inline-block;
      margin-right: 10px;
    }
  }
}
.img{
  margin-left: 15px;
  width:32px;
  height:32px;
}
.list{
  padding:0 20px;
  color:#333;
  .item{
    display: flex;
    justify-content: space-between;
    padding:20px 0;
    // border-bottom: 1px solid #F4F4F4;
  }
  .bbg{
    border-bottom: 1px solid #F4F4F4;
  }
}
</style>



